@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500&display=swap);
html{scroll-behavior:smooth}body{margin:0;padding:0;box-sizing:border-box;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}.App{font-family:"Roboto",Arial,Helvetica,sans-serif;text-align:center;color:#fff}nav{position:sticky;top:0;z-index:99}nav,footer{display:flex;align-items:center;background-color:#f9423a}nav ul,footer ul{flex:100 1 auto;display:flex;justify-content:right;padding:0}nav li,footer li{list-style:none}nav a,footer a{color:#fff}nav a:hover,footer a:hover{color:#3a92f9;font-weight:bold;transition:all 600ms}footer{justify-content:space-between}footer .credit{padding:1rem}footer .credit p{margin:0}#home,#projects{padding:64px 0 0 0}#projects{display:flex;flex-direction:column;grid-gap:4rem;gap:4rem}h1,h2,a{font-family:Montserrat,Cambria,Cochin,Georgia,Times,"Times New Roman",serif}h1{color:#f9423a}h2{color:#3a92f9;margin:0;padding:1rem}a{text-decoration:none;font-size:1.5rem;margin:1rem}a img{vertical-align:bottom}p{margin:0 0 1rem 0}p,li{line-height:150%;letter-spacing:.09rem}@media(max-width: 768px){nav{flex-direction:column}footer{flex-direction:column-reverse}nav ul,footer ul{width:100%;justify-content:space-evenly}}
.project-display{display:flex;justify-content:center;align-items:center;background-repeat:no-repeat;background-position:center;background-size:cover}.project-display:nth-child(even){flex-direction:row-reverse}.project-text-cont{display:flex;flex-direction:column;width:50%;margin:2rem;padding:2rem;background-color:rgba(0,0,0,.5);border-radius:.5rem;box-shadow:rgba(255,255,255,.35) 5px 10px 15px}.project-text-cont h1,.project-text-cont a{color:#fff}.project-text-cont a{padding:1rem 0 0}.project-text-cont ul{text-align:left;margin:auto}.project-text-cont li{list-style:square}a:hover{color:#f9423a}.project-img-cont{display:flex;justify-content:center;width:50%}.project-img-cont img{width:20rem}@media(max-width: 768px){.project-display{flex-direction:column;margin:0 0 1rem 0;height:auto}.project-display:nth-child(even){flex-direction:column}.project-text-cont,.project-img-cont{width:70%}.project-img-cont{padding:0 0 2rem 0}a{margin:0}}
#home{display:flex;justify-content:center;align-items:center;height:80vh}.grid-dodecagon{display:grid;width:30%;padding:2rem;grid-gap:.5rem;gap:.5rem;grid-template-areas:". box1 box1 ." "box2 box3 box3 box4" "box2 box3 box3 box4" "box5 box5 box6 box6" ". box7 box7."}.intoduction{width:70%}.intoduction h2{padding:1rem 1rem 0 1rem}.box1,.box7{padding:2rem 3rem}.box2,.box3,.box4{padding:3rem 2rem}.box5,.box6{padding:2rem}.yellow{background-color:#3af9a2}.primary-color{background-color:#f9423a}.secondary-color{background-color:#3a92f9}.box1{grid-area:box1}.box2{grid-area:box2}.box3{grid-area:box3}.box4{grid-area:box4}.box5{grid-area:box5}.box6{grid-area:box6}.box7{grid-area:box7}@media(max-width: 768px){#home{flex-direction:column}.grid-dodecagon{width:80%}}
